<template>
  <blur :blur-amount=40 url="https://o3e85j0cv.qnssl.com/hot-chocolate-1068703__340.jpg">
    <p class="center">
      <img src="https://o3e85j0cv.qnssl.com/hot-chocolate-1068703__340.jpg">
    </p>
  </blur>
  <panel header="朋友圈列表" :list="list"></panel>
</template>

<script>
import Blur from 'vux/src/components/blur'
import Panel from 'vux/src/components/panel'

export default {
  components: {
    Blur,
    Panel
  },
  data () {
    return {
      list: [{
        src: 'http://placeholder.qiniudn.com/60x60/3cc51f/ffffff',
        desc: '由各种物质组成的巨型球状天体，叫做星球。星球有一定的形状，有自己的运行轨道。',
        url: '/component/cell'
      }, {
        src: 'http://placeholder.qiniudn.com/60x60/3cc51f/ffffff',
        desc: '由各种物质组成的巨型球状天体，叫做星球。星球有一定的形状，有自己的运行轨道。',
        url: {
          path: '/component/radio',
          replace: false
        }
      }]
    }
  },
  ready: function () {
    this.$http.get('/post?access_token=T4qyOV2yuV1Hkx1I8yKh0wD13rEBsRtWHWXY1yuV', {}, {
      headers: {
        'X-Requested-With': 'XMLHttpRequest'
      },
      emulateJSON: true
    }).then(function (response) {
      var data = response.data
      this.list = data
    }, function (response) {
      // handle error
    })
  }
}
</script>

<style>
@import '~vux/dist/vux.css';
.center {
  text-align: center;
  padding-top: 20px;
  color: #fff;
  font-size: 18px;
}
.center img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 2px solid #ececec;
}
body {
  margin: 0px;
}

html {
    -ms-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
}

body {
    line-height: 1.6;
    font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
}

* {
    margin: 0;
    padding: 0;
}

a img {
    border: 0;
}

a {
    text-decoration: none;
}
</style>



